@import "variables";
/* shadow*/

.shadow{
  box-shadow: 1px 1px $shadowBlur $shadowColor;
  -moz-box-shadow: 1px 1px $shadowBlur $shadowColor;
  -webkit-box-shadow: 1px 1px $shadowBlur $shadowColor;
}

.shadow-block {
  @extend .shadow;
  -moz-border-radius: 5px;
  -webkit-border-radius: 5px;
  border-radius: 5px;
}
.markdown-content{
  line-height: 1.7em;
  font-size: 1.15em;
  font-weight: normal;
  word-wrap: break-word;
}

