@import "common";

.post {
  @extend .shadow-block;
  background: white;
  margin-bottom: 35px;
  position: relative;
  overflow: hidden;
}

.post .post-header {
  border-bottom: 1px solid #ebebeb;
}

.post .post-header .post-title {
  margin: 0;
  background: $widgetHeaderBgColor;
  padding: $postItemPaddingUpDown / 2 $postItemPaddingLeftRight $postItemPaddingUpDown / 2;
  font-size: 1.6em;
  line-height: 1.75em;
}

.post .post-header .post-title a {
  color: #303030;
  text-decoration: none;
}

.post .post-header .post-title a:hover {
  color: $colorAccent;
}

.post-meta {
  color: $metaColor;
  font-size: 0.85em;
  padding: $postItemPaddingUpDown/2 $postItemPaddingLeftRight $postItemPaddingUpDown / 2;
  border-top: 1px solid #ebebeb;
}

.post-description {
  font-size: 1.25em;
  line-height: 1.6em;
  color: #444443;
  margin: $postItemPaddingUpDown $postItemPaddingLeftRight $postItemPaddingUpDown;
}

.post .post-permalink {
  padding: 0 $postItemPaddingLeftRight 0;
}

.post .post-footer {
  margin-top: $postItemPaddingUpDown;
  border-top: 1px solid #ebebeb;
  padding: $postItemPaddingUpDown * 0.5 $postItemPaddingLeftRight;
}

.post .post-footer .tag-list {
  color: #959595;
}


/* post detail*/
.post-detail {
  @extend .shadow-block;
  padding: 35px 25px;
  margin-bottom: 20px;
  background: white;
}

.post-detail .post-content {
  @extend .markdown-content;
}


.post-detail img {
  box-shadow: 0 0 30px #ccc;
  -moz-box-shadow: 0 0 30px #ccc;
  -webkit-box-shadow: 0 0 30px #ccc;

  box-sizing: border-box;
  margin: 10px auto 40px auto;
  border: 1px solid #ddd;
  display: block;
  padding: 1px;
  width: 100%;
  height: auto;
}
