@import "common";

.main-header {
  text-align: center;
  background-color: $colorPrimary;
  background-size: 100% auto;
  color: #fff;
  margin-bottom: 20px;
  padding-top: 30px;
  padding-bottom: 30px;

  box-shadow: 1px 1px $shadowBlur $shadowColor;
  -moz-box-shadow: 1px 1px $shadowBlur $shadowColor;
  -webkit-box-shadow: 1px 1px $shadowBlur $shadowColor;
}

.main-header h2 {
  font-size: 2.9em;
  color: white;
  text-shadow: 5px 5px 5px $colorPrimaryDark;
}

.main-header p {
  font-size: 1.25em;
  color: white;
}

.main-header .branding img {
  max-width: 100%;
  width: 77px;
  height: 77px;
}

a:active, a:focus, a img {
  outline: none;
}

.site-navbar {
  align-items: center;
}

.site-navbar .navbar-toggle {
  border: 1px solid $colorPrimaryDark;
}

.site-navbar .navbar-toggle:focus, .site-navbar .navbar-toggle:hover {
  background-color: $colorPrimaryDark;
}

.site-navbar .navbar-toggle .icon-bar {
  background-color: #ddd
}

.site-navbar form input[type="text"] {
  border: 1px solid $colorPrimaryDark;
  color: white;
  background: transparent;
  vertical-align: middle;
}

.navbar-toggle .icon-bar {
  display: block;
  width: 22px;
  height: 2px;
  background-color: #e1e1e1;
  border-radius: 1px;
}

.navbar-nav li a {
  margin: 8px 10px;
  padding: 5px 20px;
  border: $colorPrimaryDark solid 1px;
  border-radius: 20px;
  background: $colorPrimaryDarkTransparent;
  color: white;
  text-shadow: $shadowColor 1px 1px 2px;
}

.navbar-nav li a:hover {
  color: $colorAccent;
  background: $colorPrimaryDark;
  transition: all 0.25s ease-in-out;
  -webkit-transition: all 0.25s ease-in-out;
}

.navbar-nav li a:active {
  background: $colorPrimaryDark;
}

.navbar-header a {
  font-family: 'Yatra One', cursive;
  text-decoration: none;
  font-size: 1.8em;
  color: white;
  text-shadow: 5px 5px 5px $colorPrimaryDark;
}

.navbar-header a :hover {
  color: $colorAccent;
  text-shadow: 5px 5px 5px $colorPrimaryDark;
}

header .description{
  font-family: 'Pacifico', cursive;
  font-size: 1.5em;
  text-shadow: 5px 5px 5px $colorPrimaryDark;
}