@import "common";
.widget {
  @extend .shadow-block;
  background: white;
  overflow: auto;
  margin-bottom: 35px;
}

.widget-default {

}

.widget .widget-body {
  padding: 30px $widgetPaddingLeftRight;
}

.widget .widget-footer {
  padding: 0 $widgetPaddingLeftRight 10px $widgetPaddingLeftRight;
  overflow-x: auto;
}

.widget .widget-footer .widget-meta {
  color: #959595;
  font-size: 12px;
  margin: 14px 0 5px;
}

.meta-item {
  padding: 0 10px;
  color: $metaColor;
}

.widget .widget-header {
  padding: 10px $widgetPaddingLeftRight;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  color: #333;
  border-bottom: 1px solid #ddd;
  background-color: $widgetHeaderBgColor;
}

/*widget-user*/

.description-block {
  display: block;
  margin: 10px 0;
  text-align: center;
}

.description-block > .description-header {
  margin: 0;
  padding: 0;
  font-weight: 600;
  font-size: 16px;
}

.widget .border-right {
  border-right: 1px solid #f4f4f4;
}


.widget-user {
  position: relative;
}

.widget-user .widget-user-header {
  padding: 20px;
  height: $userProfileHeight;
  border-top-right-radius: 3px;
  border-top-left-radius: 3px;
  color: white;
}

.widget-user .widget-user-username {
  margin-top: 0;
  margin-bottom: 5px;
  font-size: 25px;
  font-weight: 300;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
}

.widget-user .widget-user-desc {
  margin-top: 0;
}

.widget-user .widget-user-image {
  position: absolute;
  top: $userProfileHeight - $userAvatarRadius;
  left: 50%;
  margin-left: -45px;
}

.widget-user .widget-user-image > img {
  width: $userAvatarRadius * 2;
  height: auto;
  border: 3px solid #fff;
}

.widget-user .widget-user-footer {
  padding: $userAvatarRadius 30px 10px 30px;
}
